<h1 id="android-sqlite-database" tabindex="-1"><a class="header-anchor" href="#android-sqlite-database" aria-hidden="true">#</a> Android SQLite Database</h1>
<p>So, your application collects data from the user that you need to store somewhere? Why not in an SQlite database? Android has built-in support for that one, so why not?</p>
<div class="custom-container warning"><p class="custom-container-title">Room</p>
<p>The lecture shows the old way we communicated with SQLite databases in Android. A few years back Google released <a href="https://developer.android.com/jetpack/androidx/releases/room" target="_blank" rel="noopener noreferrer">the Room library<OutboundLink/></a> (an Object Relational Mapping library) we can use to more easily communicate with the database. However, it's still good to have knowledge about the old way, since many old applications use it.</p>
</div>
